.copy {
  position: absolute;
  width: auto;
  left: 75%;
  margin-top: 30px;
  padding: .55rem .2rem;
  font-size: .5rem;
  font-weight: 400;
  white-space: nowrap;
  color: #000;
  border-radius: 50px;
  background-color: #fff;
  box-shadow: 0 15px 35px rgba(50,50,93,.1), 0 5px 15px rgba(0,0,0,.07);
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  opacity: 0;
  transition: all .2s ease-in-out;
}

